*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}
body{
  display: flex;
  justify-content: center;
  margin-top: 10rem;
}
section{
  height: 16rem;
  width: 17rem;
  background-color: #e6e6e6;
  padding: 1rem;
}
.infoWrapper{
  height: 90%;
  width: 90%;
}
.city{
  width: 100%;
}
.cond{
  margin-top: .5rem;
  font-weight: 600;
  width: 100%;
  white-space: nowrap;
  text-transform: capitalize;
}
.temp{
  font-size: 2.6rem;
  width: 60%;
}
.wWrapper{
  font-size: .9rem;
  display: flex;
  flex-direction: row;
  max-width: 100%;
  justify-content: space-between;
}
.icon{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 0rem;
  width: 40%;
}
.extra{
  width: 45%;
  float: right;
  line-height: 1.75rem;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-top: 2.5rem;

}
.windWrapper{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-weight: 400;
  font-size: .8rem;
  width: 100%;

}
.humid{
  font-size: .9rem;
}
.pressure{
  font-size: .9rem;
}
.wrapper{
  height: 35vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.text{
  height: auto;
  margin: 0 auto;
  width: 60%;
}
.title{
  margin-bottom: 2rem;
  font-size: auto;
}
.textInput{
  display:flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.submit{
  padding: 0;
  border: none;
  background: none;
  cursor: pointer;
}
.madeBy{
  font-size: .75rem;
  font-weight: 500;
  text-align: center;
  position: fixed;
  bottom: .25rem;
  right: .35rem;
}
.heart{
  color: red;
}
.sun{
  color: orange;
}
.rain{
  color: blue;

}
.bolt{
  color: gray;
}
.cloud{
  color: white;
}
.time{
  font-size: .75rem;
  margin-top: .25rem;
  margin-bottom: 0.2rem;
}